<template>
  <div>
    <router-link class="catalog__pic" :to="{name: 'product', params: {id: product.id}}">
      <img :src="product.img" :alt="product.title">
    </router-link>
    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>
    <span class="catalog__price">
      {{product.price | numberFormat}} ₽
    </span>
    <ul class="colors colors--black">
      <li class="colors__item" v-for="colr in product.colors" :key="colr">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" :value="colr" v-model="color">
          <span class="colors__value colors__value__card" :style="{backgroundColor: colr}">
          </span>
        </label>
      </li>
    </ul>
    <ul class="sizes">
      <li class="sizes__item" v-for="mr in product.memory" :key="mr">
        <label class="sizes__label">
          <input class="sizes__radio sr-only" type="radio"  :value="mr" v-model="memory">
          <span class="sizes__value">
                    {{mr}}gb
                  </span>
        </label>
      </li>
    </ul>
  </div>
</template>

<script>
import gotoPage from '@/utils/gotoPages'
import numberFormat from '@/utils/numberFormat'
export default {
  props: ['product'],
  name: 'ProductItem',
  data () {
    return {
      color: '',
      memory: ''
    }
  },
  filters: {
    numberFormat
  },
  methods: {
    gotoPage
  }
}
</script>

