<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        {{count()}} товара
      </span>
    </div>
    <div class="content__catalog">
      <ProductFilter :page.sync="page" :memory.sync="filterMemory" :color.sync="filterColor" :category-id.sync="filterCategoryId" :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo"/>
      <section class="catalog">
        <ProductList :products="products"/>
        <BasePagination v-model="page" :per-page="productPerPage" :count="countProducts"/>
      </section>
    </div>
  </main>
</template>

<script>
import ProductFilter from '@/components/ProductFilter'
import BasePagination from '@/components/BasePagination'
import ProductList from '@/components/ProductList'
import products from '@/data/products'

export default {
  name: 'MainPage',
  components: { ProductFilter, BasePagination, ProductList },
  data () {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColor: '',
      filterMemory: [],
      page: 1,
      productPerPage: 3
    }
  },
  methods: {
    count () {
      return this.filteredProducts.length
    }
  },
  computed: {
    filteredProducts () {
      let filteredProducts = products
      if (this.filterPriceFrom > 0) {
        filteredProducts = filteredProducts.filter(product => product.price >= this.filterPriceFrom)
      }
      if (this.filterPriceTo > 0) {
        filteredProducts = filteredProducts.filter(product => product.price <= this.filterPriceTo)
      }
      if (this.filterCategoryId) {
        filteredProducts = filteredProducts.filter(product => product.categoryId === this.filterCategoryId)
      }
      if (this.filterColor) {
        filteredProducts = filteredProducts.filter(product => product.colors.includes(this.filterColor, 0))
      }
      if (this.filterMemory.length > 0) {
        let fillArr = []
        for (const i of this.filterMemory) {
          fillArr = [...fillArr, ...filteredProducts.filter(product => {
            if (product.memory && !(fillArr.find(pr => pr.id === product.id))) {
              return product.memory.includes(i, 0)
            } else {
              return false
            }
          })]
        }
        filteredProducts = fillArr
      }
      return filteredProducts
    },
    products () {
      const offset = (this.page - 1) * this.productPerPage
      return this.filteredProducts.slice(offset, offset + this.productPerPage)
    },
    countProducts () {
      return this.filteredProducts.length
    }
  }
}
</script>

