<template>
  <aside class="filter">
    <h2 class="filter__title">Фильтры</h2>

    <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="min-price" v-model.number="currentPriceFrom">
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="max-price" v-model.number="currentPriceTo">
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select">
          <select class="form__select" type="text" name="category" v-model.number="currentCategoryId">
            <option value="0">Все категории</option>
            <option :value="category.id" v-for="category in categories" :key="category.id">{{ category.title }}</option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Цвет</legend>
        <ul class="colors">
          <li class="colors__item" v-for="clr in colors" :key="clr">
            <label class="colors__label">
              <input class="colors__radio sr-only" type="radio" name="color" :value="clr" v-model="currentColor" checked="">
              <span class="colors__value" :style="{backgroundColor: clr}">
                  </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Объемб в ГБ</legend>
        <ul class="check-list">
          <li class="check-list__item" v-for="mr in memorys" :key="mr">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="volume" :value="mr" v-model="currentMemory" checked="">
              <span class="check-list__desc">
                    {{ mr }}
                    <span>(313)</span>
                  </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit">
        Применить
      </button>
      <button class="filter__reset button button--second" type="button" @click.prevent="submitReset">
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
import categories from '@/data/categories'
export default {
  data () {
    return {
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: 0,
      currentColor: '',
      currentMemory: [],
      colors: [
        '#73B6EA',
        '#FFBE15',
        '#939393',
        '#8BE000',
        '#FF6B00',
        '#FFF',
        '#000'
      ],
      memorys: [
        '8',
        '16',
        '32',
        '64',
        '128',
        '256'
      ]
    }
  },
  name: 'ProductFilter',
  props: ['priceFrom', 'priceTo', 'categoryId', 'color', 'memory', 'page'],
  computed: {
    categories () {
      return categories
    }
  },
  watch: {
    priceFrom (value) {
      this.currentPriceFrom = value
    },
    priceTo (value) {
      this.currentPriceTo = value
    },
    categoryId (value) {
      this.currentCategoryId = value
    },
    color (value) {
      this.currentColor = value
    },
    memory (value) {
      this.currentMemory = value
    }
  },
  methods: {
    submit () {
      this.$emit('update:priceFrom', this.currentPriceFrom)
      this.$emit('update:priceTo', this.currentPriceTo)
      this.$emit('update:categoryId', this.currentCategoryId)
      this.$emit('update:color', this.currentColor)
      this.$emit('update:memory', this.currentMemory)
      this.$emit('update:page', 1)
    },
    submitReset () {
      this.$emit('update:priceFrom', 0)
      this.$emit('update:priceTo', 0)
      this.$emit('update:categoryId', 0)
      this.$emit('update:color', '')
      this.$emit('update:memory', [])
      this.$emit('update:page', 1)
    }
  }
}
</script>

<style scoped>

</style>
