<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a href="#" class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': prevDisable}" aria-label="Предыдущая страница" @click.prevent="paginatePrev">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
      <a class="pagination__link" :class="{'pagination__link--current': pageNumber === page}" href="#" @click.prevent="paginate(pageNumber)">
        {{ pageNumber }}
      </a>
    </li>
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': nextDisable}" href="#" aria-label="Следующая страница" @click.prevent="paginateNext">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate'
  },
  name: 'BasePagination',
  props: ['page', 'count', 'perPage'],
  computed: {
    pages () {
      return Math.ceil(this.count / this.perPage)
    },
    prevDisable () {
      return this.page === 1
    },
    nextDisable () {
      return this.page === Math.ceil(this.count / this.perPage)
    }
  },
  methods: {
    paginate (page) {
      this.$emit('paginate', page)
    },
    paginateNext () {
      this.$emit('paginate', this.page + 1)
    },
    paginatePrev () {
      this.$emit('paginate', this.page - 1)
    }
  }
}
</script>

